function initMap() {
  var ibOptions = {
    disableAutoPan: false,
    maxWidth: 0,
    pixelOffset: new google.maps.Size(-160, 10),
    zIndex: null,
    boxStyle: {
      width: "320px",
      height: "80px"
    },
    closeBoxURL : "",
    infoBoxClearance: new google.maps.Size(1, 1),
    isHidden: false,
    pane: "floatPane",
    enableEventPropagation: false
  };
  var markers = [];
  var infoboxes = [];


  var map = new google.maps.Map(document.getElementById('map'), {
    zoom: 3,
    center: {lat: 53.1744575, lng: 50.2548807}
  });

  $('.map-popup').each(function(index, el) {
    var ELlng = el.dataset.lng,
    ELlat = el.dataset.lat,
    marker = new google.maps.Marker({
      position: {lat: parseInt(ELlat), lng: parseInt(ELlng)},
      map: map,
      icon: '/assets/images/map/map-marker.svg'
    });

    ibOptions.content = el;
    var ib = new InfoBox(ibOptions);
    marker.addListener('click', function() {
      closeInfoboxes();
      map.setCenter(marker.getPosition());
      ib.open(map, marker);
    });
    markers.push(marker);
    infoboxes.push(ib);
  });

  function closeInfoboxes() {
    infoboxes.forEach( function(element, index) {
      element.close();
    });
  }

  map.addListener('click', function() {
    closeInfoboxes();
  });

  var markerCluster = new MarkerClusterer(map, markers, {
    imageExtension: 'svg',
    imagePath: '/assets/images/map/',
    maxZoom: 14,
    minimumClusterSize: 2,
    styles: [{
      url: '/assets/images/map/1.svg',
      height: 60,
      width: 60,
      anchor: [0, 0],
      textColor: '#FFFFFF',
      textSize: 14,
      fontWeight: 'bold'
    }]
  });
}

$(document).ready(function() {
  if($('#map').length) {
    initMap();
  }

  $('.accordion--item > a').click(function(e) {
    e.preventDefault();
    $(this).parent().toggleClass('u-open');
  });

  $('.tabs--link').click(function() {
    var id = $(this).attr('href');
    $('.tabs--link').not(this).removeClass('u-active');
    $('.tabs--item').not(id).removeClass('u-open');
    $(this).addClass('u-active');
    $(id).addClass('u-open');
  });

  $('.menu--toggle').click(function(e) {
    e.preventDefault();
    $('body').toggleClass('menu-open');
  });
});